import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import element from './plugin/element'


Vue.config.productionTip = false
Vue.prototype.$EventBus = new Vue();

new Vue({
  render: h => h(App),
  router,
  store,
  element
}).$mount('#app')
