import Vue from 'vue'
import VueRouter from 'vue-router'

const Login = ()=>import ("pages/login/Login.vue");
const Home = ()=>import ("pages/home/Home.vue");
const Main = ()=>import ("pages/main/Main.vue");
const Workflow = ()=>import ("pages/workflow/Workflow.vue");
const Expansion = ()=>import ("pages/expansion/Expansion.vue");
const Authority = ()=>import ("pages/authority/Authority.vue");
const Query = ()=>import ("pages/query/Query.vue");
const Setting = ()=>import ("pages/set/Setting.vue");
const Logout = ()=> import("pages/logout/Logout.vue")
// 1.安装VueRouter
Vue.use(VueRouter)

// 2.获取路由信息
// 动态加载pages中所有的路由文件
/* const files = require.context('@/pages', true, /router\.js$/);
const routes = files.keys().map(key => {
  const page = require('@/pages' + key.replace('.', ''));
  return page.default;
}) */
const routes = [
  {
    path:"",
    redirect:"/login"
  },
  {
    path: "/login",
    name: "Login",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: Login,
  },
  {
    path:'/home',
    name:"Home",
    component:Home,
    children:[
      {
        path:'',
        component:Main
      },
      {
        path: "/workflow",
        name: "Workflow",
        component: Workflow,
      },
      {
        path: "/authority",
        name: "Authority",
        component: Authority,
      },
      {
        path: "/expansion",
        name: "Expansion",
        component: Expansion,
      },
      {
        path: "/query",
        name: "Query",
        component: Query,
      },
      {
        path: "/setting",
        name: "Setting",
        component: Setting,
      },
      {
        path:"/logout",
        name:"Logout",
        component:Logout
      }
    ]
  },
  
]
// 3.创建路由对象
const router = new VueRouter({
  /* mode: 'history', */
  routes
})

// 4.导出
export default router
